import React from "react";
import makeStyles from "@mui/styles/makeStyles";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import Button from "@mui/material/Button";
import Stack from "@mui/material/Stack";
import Container from "@mui/material/Container";
import { NTRL_000, NTRL_100, NTRL_300 } from "../../../theme/colors";
import { formatNumber } from "../../../helpers/common/utility";

const useStyles = makeStyles(() => ({
  root: {
    position: "fixed",
    padding: "32px 0",
    bottom: 0,
    left: 0,
    right: 0,
  },
  box: {
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
    borderRadius: "6px",
    backgroundColor: NTRL_000,
    padding: "8px",
  },
}));

const TotalPayment = (props) => {
  const classes = useStyles();
  const { total, textAction, disabled, action } = props;

  return (
    <div className={classes.root}>
      <Container>
        <Stack spacing={1}>
          <Box className={classes.box}>
            <Typography variant="body1">Total</Typography>
            <Typography variant="subtitle1">
              {total ? formatNumber(total) : "IDR 0"}
            </Typography>
          </Box>
          <Button variant="outlined" disabled={disabled} onClick={action}>
            {textAction}
          </Button>
        </Stack>
      </Container>
    </div>
  );
};

export default TotalPayment;
